import React, { Component } from 'react';
import SearchBar from './SearchBar';
import youtube from './api/youtube';
import VideoList from './VideoList';
import VideoDetails from './VideoDetails';

class App extends Component {
  state = {
    videos: [],
    selectedVideo: null
  }

  componentDidMount() {
    this.onSearchSubmit('Hospital playlist ost')
  }

  onSearchSubmit = async (term) => {
    const respond = await youtube.get('/search', {
      params: {
        q: term
      }
    })

    this.setState({ videos : respond.data.items })

    console.log(respond.data.items);
  }

  selectVideo = (selected) => {
    this.setState({ selectedVideo: selected })
    console.log(selected);
    console.log(this.state.selectedVideo);
  }

  render() {
    if(this.state.videos) {
      return (
        <div className="space-y-4">
          <h1>Youtube Videos Browser</h1>
          <SearchBar 
          onSearchSubmit={this.onSearchSubmit}
          />
          <div className="row">
            <VideoDetails 
            video={this.state.selectedVideo}
            videos={this.state.videos}
            />
            <VideoList 
            videos={this.state.videos}
            onVideoSelect={this.selectVideo}
            />
          </div>
        </div>
      )
    } else {
      return <div>Loading...</div>
    }
    
  }
}

export default App;
